import * as React from "react"

export default function Coffee() {

    const coffeeImage = {
        width: '10 rem'
    }
   
    /**
     * Fetch from https://api.sampleapis.com/coffee/hot
     * Transform into JSX
     */


    /**
     * Run once, as soon as the page has finished loading
     * Fetch from https://api.sampleapis.com/coffee/hot
     * Needs to be asynchronous because we used await 
     */

    let items = [] // items is just a variable, not a state
    let [coffeeTitles, setCoffeeTitles] = React.useState([])
    let [coffeeDescription, setCoffeeDescription] = React.useState([])

    React.useEffect(async()=>{

        let res = await fetch('https://api.sampleapis.com/coffee/hot')
        let coffees = await res.json() // convert fetched data into json
        for (let i=0; i<coffees.length; i++) {
            items.push(<li>
                <div style={{width: "40 px", overflow:"hidden"}}>
                    <div style={{width:"11rem",float:"left"}}> <img src={coffees[i].image}/></div>
                    <div><b>{coffees[i].title}</b> - {coffees[i].description}</div>
                </div>
                </li>)
        }

        setCoffeeTitles(items)
    },[])

    return (
        <>
            <h1>Coffee</h1>
            <ul>{coffeeTitles}</ul>
        </>
    )
    
}